import React from 'react'
import Link from 'gatsby-link'
import mepic from './wesleysaur_stage.jpeg'

const AboutMe = () => (
	<div>
		<h1>About Me!</h1>
		<p>I am a senior electrical engineering student at UC San Diego, with a passion for music and technology. I love DJing, 
		producing and writing music, cooking, gaming (video and board games), and noodling with both vintage and the latest technology.
		I like to consider myself a full stack engineer, as I am writing this page in react, but I have no problem whipping out the 
		soldering iron or drill if need be. </p>
		<img src={mepic} alt="Me DJing" />
	</div>
)

export default AboutMe



